
.contact-me-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #202020;
    color: #ffffff;
    font-size: 1.5rem;
    margin-bottom: 80px;
    width: 100% !important;
    padding: 10px;
}

.contact-me-button {
    padding: 10px;
    width: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #202020;
    border: solid 2px #ffffff;
    border-radius: 5px;
    margin-bottom: 20px;
    font-size: 1rem;
}

.contact-me-title {
    color: #ffffff !important;
    font-size: 10rem;
    margin-bottom: 20px;
}

/* Small screens (up to 600px) */
@media only screen and (max-width: 600px) {
    .contact-me-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #202020;
        color: #ffffff;
        width: 100% !important;
        padding: 10px;
    }
}

/* Tablets (601px - 768px) */
@media only screen and (min-width: 601px) and (max-width: 768px) {
    .contact-me-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #202020;
        color: #ffffff;
        font-size: 1.5rem;
        margin-bottom: 80px;
        width: 100% !important;
        padding: 10px;
    }
    
}

