.card-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 1rem;
    margin: 1rem;
    width: 300px;
    height: 400px;
}

.card-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.card-image {
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 50%;
}

.card-title {
    font-size: 1.5rem;
    font-weight: bold;
    margin-top: 1rem;
}

.card-text{
    font-size: 0.9rem;
    margin-top: 1rem;
    text-align: center;
}
