.pod-container {
    padding: 1rem;
    max-width: 300px;
    min-height: 200px;
    height: 250px;
    margin: 1rem;
    background-color: #bcae70ba;
    border-radius: 15px;
    border: 1px solid #94947eba;
}

.pod-title {
    font-size: 0.8rem;
    font-weight: bold;
}

.pod-content {
    font-size: 0.8rem;
    justify-content: start;
    text-align: start;
}

div {
    p {
        font-size: 0.8rem;
        font-family: 'Source Sans Pro';
    }

    ul {
        list-style-type: disc !important;
        padding: 0;

        li {
            padding-bottom: 0px;
            font-size: 0.8rem;
            font-family: 'Source Sans Pro';
            list-style-type: disc !important;
        }
    }
}

/* Small screens (up to 600px) */
@media only screen and (max-width: 600px) {
    .pod-container {
        padding: 1rem;
        width: 300px;
        max-height: 500px;
        margin: 1rem;
        background-color: #bcae70ba;
        border-radius: 15px;
        border: 1px solid #94947eba;
    }
    div {
        p {
            font-size: 0.8rem;
            font-family: 'Source Sans Pro';
        }
    
        ul {
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            li {
                padding: 10px;
                padding-bottom: 0px;
                font-size: 0.8rem;
                font-family: 'Source Sans Pro';
            }
        }
    }
}