.hero-image {
    background-image: url(../../assets/hero-image-dev.png);
    background-color: #cccccc;
    max-height: 400px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    border-radius: 50%;
    margin: 10px auto;
}

.hero-title {
    display: block;
    text-align: center !important;
    color: #202020;
    font-size: 20px;
    text-shadow: 0px 0px 10px #8c8888;
}

.hero-content {
    display: block;
    text-align: center !important;
    margin: 0 auto;
    color: #202020;
    width: 70%;
}

.hero-description {
    display: block;
    text-align: center !important;
    margin: 0 auto;
    color: #202020;
    width: 90%;
    font-size: 18px;
}

button {
    background-color: #729ECF;
    color: white;
    padding: 10px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
}

button:hover {
    background-color: #3579c6;
}
.hero-button {
    margin: 10px auto;
}

/* Small screens (up to 600px) */
@media only screen and (max-width: 600px) {
    .hero-image {
        background-image: url(../../assets/hero-image-dev.png);
        background-color: #cccccc;
        max-height: 250px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        border-radius: 50%;
        margin: 10px auto;
    }
}