.home-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100% !important;
}

.pods-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: flex-start;
    max-width: 920px;
    justify-content: center;
    margin: auto;
}

.cards-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    max-width: 1200px;
    margin: auto;
    padding: 0px;
}

.chatbot-container {
    display: block;
    text-align: center;
    margin: auto;
    width: 50%;
}

.chatbot {
    display: block;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 300px;
    width: 100%;
    min-height: 500px;
}

@media (max-width: 768px) {
    .chatbot-container {
        display: block;
        text-align: center;
        margin: auto;
        width: 100%;
        margin-left: 10px;
        margin-right: 7px;
    }
}