.contact-form {
    width: 100%;
    max-width: 400px;
    margin: auto;
    margin-bottom: 20px;
}

.contact-form input,
.contact-form textarea {
    width: 100%;
    margin: 10px 0;
    padding: 8px;
    border: none;
    background-color: transparent;
    border-bottom: 1px solid #202020;
}

button {
    background-color: #729ECF;
    color: white;
    padding: 10px;
    border: none;
    cursor: pointer;
}

button:hover {
    background-color: #3579c6;
}

.status-message {
    color: green;
    margin-top: 10px;
}

label {
    color: #202020;
    text-align: left;
    display: block;
}

/* Small screens (up to 600px) */
@media only screen and (max-width: 600px) {
    .contact-form {
        padding: 10px;
        width: 100%;
        max-width: 300px;
        margin: auto;
        margin-bottom: 20px;
    }
}

/* Tablets (601px - 768px) */
@media only screen and (min-width: 601px) and (max-width: 768px) {
    .contact-form {
        width: 100%;
        max-width: 300px;
        margin: auto;
        margin-bottom: 20px;
    }
}