* {
    margin: 0;
    padding: 0;
}

.header-container {
    background-color: #d4c9bc;
    color: #ffffff;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.header-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
}

li {
    list-style: none;
    display: inline-block;
    padding: 15px;
    cursor: pointer;
    padding-top: 10px;
    font-size: 18px;
    font-family: 'Source Serif Pro';
    color: #202020;
}

.header-logo {
    display: inline-block;
    width: 80px;
    height: 80px;
    border-radius: 3px;
    margin: 15px;
}

.link {
    text-decoration: none;
    font-size: 18px;
    font-family: 'Source Serif Pro';
    color: #202020;
}

nav {
    display: flex;
    align-items: center;
}

.menu-link {
    text-decoration: none;
    font-size: 18px;
    font-family: 'Source Serif Pro';
    color: #202020;
    cursor: pointer;
    padding: 10px;
}

.menu-link:hover {
    background-color: #cfa372;
    border-radius: 25px;
}

.hamburger {
    display: none;
}

/* Small screens (up to 600px) */
@media only screen and (max-width: 600px) {
    .header-container {
        background-color: #d4c9bc;
        color: #ffffff;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }

    .header-menu {
        display: flex;
        flex-direction: column;
        margin: auto;
    }

    .social-icon {
        display: block;
        justify-content: space-between;
        align-items: center;
        margin-bottom: -10px;
    }

    .header-logo {
        display: inline-block;
        width: 40px;
        height: 40px;
        border-radius: 3px;
    }

    ul {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        width: 60%;
        text-align: center;
        justify-content: center;

        li {
            list-style: none;
            display: inline-block;
            cursor: pointer;
            font-size: 10px;
            font-family: 'Source Serif Pro';
            color: #202020;
            padding: 8px;
        }
    }

    .menu-link {
        text-decoration: none;
        font-family: 'Source Serif Pro';
        color: #202020;
        cursor: pointer;
    }
}

/* Tablets (601px - 768px) */
@media only screen and (min-width: 601px) and (max-width: 768px) {
    .header-container {
        background-color: #d4c9bc;
        color: #ffffff;
        padding: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }
}

@media (max-width: 768px) {
    .menu {
        display: none;
        flex-direction: column;
        background: #333;
        position: absolute;
        top: 50px;
        left: 0;
        width: 100%;
    }

    .header-menu {
        display: none;
        flex-direction: column;
        position: absolute;
        top: 50px;
        left: 0;
        width: 100%;
    }

    .header-menu-open{
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 70px;
        right: 0;
        width: 40%;
        background-color: #c0b29e;
        border: solid 2px #cdc2b5;
    }

    nav {
        display: flex;
        align-items: center;
    }

    .menu-link:hover {
        background-color: #cfa372;
        border-radius: 5px;
    }

    .menu-link {
        padding: 5px;
        text-align: center;
        display: block;
        flex-direction: row;
        text-decoration: none;
        font-size: 18px;
        font-family: 'Source Serif Pro';
        color: #202020;
        cursor: pointer;
    }

    .social-icon {
        display: block;
        align-items: center;
    }

    .hamburger {
        display: block;
        margin-right: 20px;
        cursor: pointer;
        font-size: 35px;
    }

    /* Toggle menu when checked */
    #menu-toggle:checked~.menu {
        display: flex;
    }
}